	module.exports = {"pathContext":{"docHtml":"<h1>string-width <a href=\"https://travis-ci.org/sindresorhus/string-width\"><img src=\"https://travis-ci.org/sindresorhus/string-width.svg?branch=master\" alt=\"Build Status\"></a></h1>\n<blockquote>\n<p>Get the visual width of a string - the number of columns required to display it</p>\n</blockquote>\n<p>Some Unicode characters are <a href=\"https://en.wikipedia.org/wiki/Halfwidth_and_fullwidth_forms\">fullwidth</a> and use double the normal width. <a href=\"https://en.wikipedia.org/wiki/ANSI_escape_code\">ANSI escape codes</a> are stripped and doesn't affect the width.</p>\n<p>Useful to be able to measure the actual width of command-line output.</p>\n<h2>Install</h2>\n<pre><code>$ npm install string-width\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">const stringWidth = require('string-width');\n\nstringWidth('古');\n//=> 2\n\nstringWidth('\\u001b[1m古\\u001b[22m');\n//=> 2\n\nstringWidth('a');\n//=> 1\n</code></pre>\n<h2>Related</h2>\n<ul>\n<li><a href=\"https://github.com/sindresorhus/string-width-cli\">string-width-cli</a> - CLI for this module</li>\n<li><a href=\"https://github.com/sindresorhus/string-length\">string-length</a> - Get the real length of a string</li>\n<li><a href=\"https://github.com/sindresorhus/widest-line\">widest-line</a> - Get the visual width of the widest line in a string</li>\n</ul>\n<h2>License</h2>\n<p>MIT © <a href=\"https://sindresorhus.com\">Sindre Sorhus</a></p>","docPath":"argo-ci/node_modules/boxen/node_modules/string-width/readme","proj":"argo-ci"}}

